
import React, { Component, PropTypes } from 'react';


class PasswordInput extends Component {

  constructor(props) {
    super(props);

    this.state = {
      passwordStatusText : props.showText,
      inputType          : 'password'
    };

    this.togglePassword = _togglePassword.bind(this);
  };

  render() {
    const { passwordStatusText, inputType, defaultLabel }                         = this.state;
    const { id, labelText, placeholder, error, fancy, infoMessage, errorMessage } = this.props;

    let appliedLabelStyle;
    let appliedStyleLine;
    let appliedInputStyle;


    switch (inputType) {
      case 'error':
        appliedLabelStyle = 'pe-textLabelInput__label--label_error';
        appliedInputStyle = (fancy) ? 'pe-textInput--input_error' : 'pe-textInput--basic_error';
        appliedStyleLine  = (fancy) ? 'pe-inputError_underline'   : '';
        break;
      case 'disabled':
        appliedLabelStyle = 'pe-textLabelInput__label--label-disabled';
        appliedInputStyle = (fancy) ? 'pe-textInput' : 'pe-textInput--basic';
        appliedStyleLine  = '';
        break;
      case 'readOnly':
        appliedLabelStyle = 'pe-textLabelInput__label';
        appliedInputStyle = 'pe-textInput--input_readonly';
        appliedStyleLine  = '';
        break;
      default:
        appliedLabelStyle = 'pe-textLabelInput__label';
        appliedInputStyle = (fancy) ? 'pe-textInput'       : 'pe-textInput--basic';
        appliedStyleLine  = (fancy) ? 'pe-input_underline' : '';
    };


    return(
      <div>
        <label className={appliedLabelStyle} htmlFor={`password-${id}`}>{labelText}</label>
        <input className={appliedInputStyle} type={inputType} id={`password-${id}`} placeholder={placeholder} />
        <span className={appliedStyleLine}></span>
        <button className={fancy ? "pe-textInput__showButton":'pe-textInput__showButton-basic'} id="showbutton" onClick={this.togglePassword}>{passwordStatusText}</button>
        {infoMessage  && <span className="pe-input--info_message">{infoMessage}</span>}
        {errorMessage && <span className="pe-input--error_message">{errorMessage}</span>}
      </div>
    )
  };
};


export default PasswordInput;


PasswordInput.propTypes = {
  error        : PropTypes.bool,
  id           : PropTypes.string,
  labelText    : PropTypes.string,
  placeholder  : PropTypes.string,
  showText     : PropTypes.string,
  hideText     : PropTypes.string,
  infoMessage  : PropTypes.string,
  errorMessage : PropTypes.string
};


function _togglePassword() {
  const { showText, hideText } = this.props;
  const inputType = (this.state.inputType === 'password') ? 'text' : 'password';
  const passwordStatusText = (this.state.passwordStatusText === showText) ? hideText : showText;
  this.setState({inputType, passwordStatusText})
};



